import React, { useEffect, useRef, useState } from "react";
import HotSugar from "./media/HotSugar.mp3";
import "./media/BACKTO1982.TTF";
import "./index.css";
import PacGrid from "./components/PacGrid";

const MusicApp = () => {
  const [isPlaying, setIsPlaying] = useState(false);
  const player = useRef(new Audio());
  useEffect(() => {
    player.current.src = HotSugar;
  }, []);
  const play = () => {
    if (isPlaying) {
      player.current.pause();
    } else {
      player.current.play();
    }
    setIsPlaying(!isPlaying);
  };
  return (
    <>
      <div className="music-app">
        <div className="music-bg">
          <div className="music-title" onClick={play}>
            Hot Sugar
          </div>
          <PacGrid className="grid" />
          {/* <div className="grid"></div> */}
        </div>
      </div>
    </>
  );
};

export default MusicApp;
