import React, { useRef, useState, useEffect } from "react";
import PacGhost from "../../pac/components/PacGhost";

const styles = {
  display: "flex",
  flexWrap: "wrap",
};
const PacGrid = ({ className }) => {
  const divRef = useRef(null);
  const [dim, setDim] = useState({
    xCount: 0,
    yCount: 0,
  });
  useEffect(() => {
    if (divRef.current) {
      const { offsetWidth, offsetHeight } = divRef.current;
      divRef.current.style.width =
        divRef.current.scrollWidth - (offsetWidth % 47) + "px";
      // offsetWidth + (47 - (offsetWidth % 47)) + "px";
      const xCount = Math.floor(offsetWidth / 47);
      const yCount = Math.floor(offsetHeight / 45);
      setDim({ xCount, yCount });
    }
  }, []);
  return (
    <div ref={divRef} className={className}>
      {dim.xCount &&
        [...Array(dim.xCount).keys()].map((k) => {
          const position = {
            position: "absolute",
            left: k * 47,
          };
          return (
            <>
              <PacGhost position={{ ...position, top: 0 }} />
              <PacGhost position={{ ...position, bottom: 0 }} />
            </>
          );
        })}
      {dim.yCount &&
        [...Array(dim.yCount).keys()].map((k) => {
          if (k === 0 || k === dim.yCount) return;
          const position = {
            position: "absolute",
            top: k * 45,
          };
          return (
            <>
              <PacGhost position={{ ...position }} />
              <PacGhost position={{ ...position, right: 0 }} />
            </>
          );
        })}
    </div>
  );
};

export default PacGrid;
